#header-container {
    font-family: 'Roboto';
    margin: 0;
    width: 100%;
    height: 100%;
}

#header-logo {
  position: absolute;
  font-family: 'Roboto';
  font-size: 60px;
  font-weight: bold;
  float: left;
  top: 0;
  padding: 0 70px 0px 50px;
  cursor: pointer;
  z-index: 5;
  color: #006D3C;
  border-radius: 0px 70px 70px 0px;
	background: linear-gradient(to right, #006D3C, #00D524, #1CC87C, #00E1A0, #00B39F, #00B0B0);
}

#header-logo p { font-size: 20px; margin-top: 5px; margin-left: 5px; }

#header-list-active { color: #3498db }
#header-list-active:hover { color: gray }

.list {
    text-decoration: none;
    color: black;
}

.list:hover { color: gray }

.header-list-link li {
    color: #1cb092;
    font-size: 25px;
    font-family: 'Roboto' !important;
    right: 10px;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
}

@media only screen and (min-width: 1150px){
    .changeList {
        display: inline-block;
        float: right;
    }

    #header-logo { margin: 0 !important }

    #list-container { text-align: right }
    #dropdown-lines { display: none }

    #header-list li {
        display: inline-block;
        cursor: pointer;
        margin: 20px;
    }
}

@media only screen and (min-width: 600px) and (max-width: 1150px){
    #dropdown-lines {
        position: relative;
        display: inline-block;
        float: right;
        margin: 15px 5px 0px 0px;
        cursor: pointer;
        z-index: 2;
    }

    #header-logo { font-size: 5vw }
    #header-logo p { font-size: 1.5vw }

    #dropdown-lines div {
        background-color: black;
        width: 35px;
        height: 2px;
        margin: 7px;
        transition: 1s;
    }
    
    #header-list {
        transition: 1s;
        padding: 0;
        height: 100%;
        width: 0;
        position: fixed; 
        z-index: 1; 
        top: 8vw;
        right: 0;
        overflow-x: hidden; 
        background-color: white;
    }

    #header-list li {
        margin-left: 50px;
        margin-top: 25px;
        list-style: none;
        font-size: 30px;
        cursor: pointer;
    }

    #header-list li:hover {
        color: gray;
    }

    .changeList #header-list {
        width: 100%;
    }

    .changeIcon #line1 {
        -webkit-transform: rotate(-45deg) translate(-5px, 5px);
        transform: rotate(-45deg) translate(-5px, 5px);
    }
      
    .changeIcon #line2 {opacity: 0;}
      
    .changeIcon #line3 {
        -webkit-transform: rotate(45deg) translate(-8px, -8px);
        transform: rotate(45deg) translate(-8px, -8px);
    }
}

@media only screen and (max-width: 600px){
    #header-logo { font-size: 7vw }
    #header-logo p { font-size: 2.5vw }
    #header-logo { padding: 0 10vw 0 10vw }

    #header-list {
        transition: 1s;
        padding: 0;
        height: 100vh;
        width: 0;
        position: fixed; 
        z-index: 1; 
        top: 12vw; 
        right: 0;
        overflow-x: hidden; 
        background-color: white;
    }

    #header-list li {
        margin-left: 50px;
        margin-top: 25px;
        list-style: none;
        font-size: 25px;
        cursor: pointer;
    }

    #header-list li:hover { color: gray }
    .changeList #header-list { width: 100vw }

    #dropdown-lines {
        display: inline-block;
        position: relative;
        z-index: 2;
        float: right;
        margin: 15px 5px 0px 0px;
        cursor: pointer;
    }

    #dropdown-lines div {
        background-color: black;
        width: 30px;
        height: 2px;
        margin: 7px;
        transition: 1s;
    }

    .changeIcon #line1 {
        -webkit-transform: rotate(-45deg) translate(-5px, 5px);
        transform: rotate(-45deg) translate(-5px, 5px);
    }
      
    .changeIcon #line2 {opacity: 0;}
      
    .changeIcon #line3 {
        -webkit-transform: rotate(45deg) translate(-8px, -8px);
        transform: rotate(45deg) translate(-8px, -8px);
    } 
}